<template>
    <div class="container">
        <div class="main__users">
            <div class="main__users-item" v-for="user in users" :key="user.id">
                <img src="@/assets/images/user.png" alt="">
                <div class="main__users-item-info">
                    <p>Name: {{ user.name }}</p>
                    <p>Username: {{ user.username }}</p>
                    <p>Email: {{ user.email }}</p>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
       data() {
            return {
                users: null
            }
       },
       created() {
        this.getUsers()
       },
       methods: {
            async  getUsers() {
                let res = await fetch('https://jsonplaceholder.typicode.com/users')
                let data = await res.json()
                this.users = data
            }
       },
    }
</script>